.movie_goback_btn {
  padding: 12px 48px;
  font-size: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: all 300ms ease-in-out;
  background-color: rgb(23, 23, 23);
  color: white;
  border: none;
  border-radius: 24px;
  cursor: pointer;
  display: block;
  margin-top: 20px;
  max-width: 75px;
  text-align: center;
  text-decoration: none;
}

.movie_goback_btn:hover {
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.7);
  transform: scale(1.02);
}
