.form {
  margin: 0 auto;
  display: flex;
  justify-content: center;
  margin-top: 40px;
  margin-bottom: 40px;
}

.input_wrap {
  height: 42px;
  position: relative;
  width: 320px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

.input {
  outline: none;
  width: 290px;
  margin: 0 auto;
  height: 42px;
  padding: 0 14px;
  border: 1px solid black;
  border-radius: 5px;
  font-size: 16px;
}

.search_svg {
  width: 24px;
  height: 24px;
  stroke: white;
  fill: rgb(193, 187, 187);
}

.form_btn {
  height: 44px;
  width: 44px;
  border-radius: 5px;
  position: absolute;
  border: 1px solid black;
  right: 0;
  cursor: pointer;
  transition: all 300ms ease-in-out;
  background-color: rgb(56, 56, 57);
}
.form_btn:hover {
  background-color: black;
}
